export const increaseAction = () => {
    return { type: 'increase' }
}

export const minAction = () => {
    return { type: 'min' }
}

export const incrementAsync = () => {
    return { type: 'incrementAsync' }
}

export const getData = (email, password) => {
    return {
        type: 'getData',
        email,
        password,
    }
}



